import { ajax } from 'rxjs/ajax';
import { map } from 'rxjs/operators';

const { VUE_APP_API_ENDPOINT } = process.env;

const state = {
	restaurantList: [],
	nextPageToken: null,
	selectedRestaruant: null,
	selectedRestaurantPhotos: [],
};

const mutations = {
	setRestaurantList(state, payload) {
		// state.restaurantList.push(payload);
		state.restaurantList = [...state.restaurantList, ...payload];
	},
	setNextPageToken(state, payload) {
		state.nextPageToken = payload;
	},
	setSelectedRestaurant(state, payload) {
		state.selectedRestaruant = payload;
	},
	setSelectedRestaurantPhotos(state, payload) {
		state.selectedRestaurantPhotos = payload;
	},
	cleanList(state) {
		state.restaurantList.length = 0;
		state.nextPageToken = null;
	},
};

const actions = {
	async fetchRestaurantList(context, payload) {
		try {
			const options = {
				url: `${VUE_APP_API_ENDPOINT}/restaurants`,
				method: 'POST',
				headers: {
					'Content-Type': 'application/json',
				},
				body: payload,
			};
			const ajaxObject$ = ajax(options).pipe(
				map(({ response }) => {
					return response;
				}),
			);
			ajaxObject$.subscribe(
				(data) => {
					data.results.sort((a, b) => {
						let result = 0;
						if (a.user_ratings_total > b.user_ratings_total) {
							result = -1;
						} else {
							result = 1;
						}
						return result;
					});
					payload.onSuccess();
					context.commit('setRestaurantList', data.results);
					context.commit('setNextPageToken', data.next_page_token);
				},
				(error) => {
					console.log('text search error', error);
				},
				() => { console.log('seach end'); },
			);
		} catch (error) {
			console.error('fetch restaurant list error', error);
		}
	},
	async fetchNextPage(context, callback) {
		try {
			const payload = {
				token: context.state.nextPageToken,
			};
			const options = {
				url: `${VUE_APP_API_ENDPOINT}/more`,
				method: 'POST',
				headers: {
					'Content-Type': 'application/json',
				},
				body: payload,
			};
			const ajaxObject$ = ajax(options).pipe(
				map(({ response }) => {
					return response;
				}),
			);
			ajaxObject$.subscribe(
				(data) => {
					data.results.sort((a, b) => {
						let result = 0;
						if (a.user_ratings_total > b.user_ratings_total) {
							result = -1;
						} else if (!a.user_ratings_total || !b.user_ratings_total) {
							result = !a.user_ratings_total ? 1 : -1;
						} else {
							result = 1;
						}
						return result;
					});
					const hasNext = !!data.next_page_token;
					callback(hasNext);
					context.commit('setRestaurantList', data.results);
					context.commit('setNextPageToken', data.next_page_token);
				},
				(error) => {
					console.log('text search error', error);
				},
				() => { console.log('seach end'); },
			);
		} catch (error) {
			console.error('fetch next page error', error);
		}
	},
	async fetchSelectedRestaurant(context, payload) {
		try {
			const options = {
				url: `${VUE_APP_API_ENDPOINT}/restaurant/${payload}`,
				method: 'get',
			};
			const ajaxObject$ = ajax(options).pipe(
				map(({ response }) => {
					return response;
				}),
			);
			ajaxObject$.subscribe(
				(data) => {
					context.commit('setSelectedRestaurant', data.result);
				},
				(error) => {
					console.log('text search error', error);
				},
				() => { console.log('seach end'); },
			);
		} catch (error) {
			console.error('fetch next page error', error);
		}
	},
	async fetchPhotos(context, payload) {
		try {
			const options = {
				url: `${VUE_APP_API_ENDPOINT}/photos`,
				method: 'POST',
				headers: {
					'Content-Type': 'application/json',
				},
				body: payload,
			};
			const ajaxObject$ = ajax(options).pipe(
				map(({ response }) => {
					return response;
				}),
			);
			ajaxObject$.subscribe(
				(data) => {
					context.commit('setSelectedRestaurantPhotos', data.photos);
				},
				(error) => {
					console.log('text search error', error);
				},
				() => { console.log('seach end'); },
			);
		} catch (error) {
			console.log('fetch next page error', error);
		}
	},
	async searchByText(context, payload) {
		const options = {
			url: `${VUE_APP_API_ENDPOINT}/search`,
			method: 'POST',
			headers: {
				'Content-Type': 'application/json',
			},
			body: payload.params,
		};
		const ajaxObject$ = ajax(options).pipe(
			map(({ response }) => {
				return response;
			}),
		);
		ajaxObject$.subscribe(
			(data) => {
				const isEmpty = data.results.length === 0;
				payload.onSuccess(isEmpty);
				context.commit('setRestaurantList', data.results);
				context.commit('setNextPageToken', data.next_page_token);
				console.log('response', data);
			},
			(error) => {
				payload.onFailure();
				console.log('text search error', error);
			},
			() => { console.log('seach end'); },
		);
	},
};

const getters = {
	getNextPageToken: (state) => {
		return state.nextPageToken;
	},
	getSelectedRestaurant: (state) => {
		return state.selectedRestaruant;
	},
	getSelectedRestaurantPhotos: (state) => {
		return state.selectedRestaurantPhotos.length > 0
			? state.selectedRestaurantPhotos
			: null;
	},
	getTotalPagination: (state) => {
		return state.restaurantList.length > 1
			? state.restaurantList.length
			: 1;
	},
	getRestaurants: (state) => {
		return state.restaurantList;
	},
	isFufillRestaurants: (state) => {
		return state.restaurantList.length > 0;
	},
};

export default {
	state,
	mutations,
	actions,
	getters,
};
